import React, { Component, Suspense } from 'react';
import { OverlayTrigger, Tooltip, Table } from 'react-bootstrap';
import moment from 'moment';
import { saveAs } from 'file-saver';
import { getInvoiceList, invoiceDownload } from '../../../../Services/Actions/invoiceActions'
import { InnerLoader } from '../../CommonComponents/InnerLoader';
const ViewInvoice = React.lazy(() => import('./Elements/ViewInvoice'));
const NoInvoiceFound = React.lazy(() => import('./Elements/NoInvoiceFound'));

class Invoice extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        viewInvoice: false,
        viewInvoiceData: null,
        invoiceDownload: 0,
        invoiceList: [],
        invoiceRequest: true,
        invoiceDownloadRequest: false,
        invoicePrintRequest: false
    };
    componentDidMount() {
        this.onLoadInvoice()
    }

    viewInvoice = (data) => event => {
        this.setState({
            viewInvoice: true,
            viewInvoiceData: data
        })
    }
    viewInvoiceCallback = (dataEvent) => {
        this.setState({
            viewInvoice: dataEvent,
            viewInvoiceData: null
        })
    }

    clickToInvoiceDownload = (id) => event => {
        this.setState({
            invoiceDownload: 1
        })
        var self = this;
        invoiceDownload(id).then((response) => {
            self.setState({
                invoiceDownload: 2
            })
            const file = new Blob([response.data], { type: 'application/pdf' });
            saveAs(file, `${id}.pdf`);
            setTimeout(() => {
                self.setState({
                    invoiceDownload: 0
                })
            }, 3000);
        })


    }

    downloadInvoice = (id) => event => {
        this.setState({
            invoiceDownloadRequest: true
        })
        var self = this;
        invoiceDownload(id).then((response) => {
            const file = new Blob([response.data], { type: 'application/pdf' });
            saveAs(file, `${id}.pdf`);
            setTimeout(() => {
                self.setState({
                    invoiceDownloadRequest: false
                })
            }, 3000);
        })


    }
    printInvoice = (id) => event => {
        this.setState({
            invoicePrintRequest: true
        })
        var self = this;
        invoiceDownload(id).then((response) => {
            const file = new Blob([response.data], { type: 'application/pdf' });
            const fileURL = URL.createObjectURL(file);
            window.open(fileURL, '_blank');
            setTimeout(() => {
                self.setState({
                    invoicePrintRequest: false
                })
            }, 3000);

        })


    }
    onLoadInvoice = () => {
        var self = this;
        getInvoiceList().then((responseData) => {
            const { response } = responseData.data
            self.setState({ invoiceList: response, invoiceRequest: false })
        })
    }


    render() {
        const { viewInvoice, invoiceDownload, invoiceList, invoiceRequest, viewInvoiceData, invoiceDownloadRequest, invoicePrintRequest } = this.state;
        return <div className="">
            <div className="mt-xxl-3 mt-2">
                <h3 className="fs-6 text-gray-800">Invoice</h3>
            </div>
            <div className="card mb-5">
                <div className="card-body p-lg-4 p-3" style={{ minHeight: 'calc(100vh - 400px)' }}>
                    {
                        invoiceRequest ? <InnerLoader /> : invoiceList && invoiceList.length > 0 ? <Table responsive className="mb-0">
                            <thead>
                                <tr>
                                    <th className="p-lg-4 p-sm-3 p-2">Date</th>
                                    <th className="p-lg-4 p-sm-3 p-2">Invoice Id</th>
                                    <th className="p-lg-4 p-sm-3 p-2">Plan</th>
                                    <th className="p-lg-4 p-sm-3 p-2">Amount</th>
                                    <th className="p-lg-4 p-sm-3 p-2">Status</th>
                                    <th className="text-end p-lg-4 p-sm-3 p-2" align="right">Action</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    invoiceList.map((invoiceItem, invoiceIndex) => {
                                        return <tr className="align-middle" key={invoiceIndex}>
                                            <td className="p-lg-4 p-sm-3 p-2 text-nowrap">{moment(invoiceItem.invoiceDate).format("DD MMM YYYY")}</td>
                                            <td className="p-lg-4 p-sm-3 p-2 text-nowrap">{invoiceItem.invoiceId}</td>
                                            <td className="p-lg-4 p-sm-3 p-2 text-nowrap">{invoiceItem.plan}</td>
                                            <td className="p-lg-4 p-sm-3 p-2 text-nowrap">{invoiceItem.invoiceView.currencyValue}{invoiceItem.invoiceView.amountValue}</td>
                                            <td className="p-lg-4 p-sm-3 p-2 text-nowrap">
                                                <span className="badge badge-light-success">Paid</span>
                                            </td>
                                            <td className="p-lg-4 p-sm-3 p-2 text-nowrap text-end">
                                                <OverlayTrigger placement="top" overlay={<Tooltip>View</Tooltip>}>
                                                    <button type="button" className="btn btn-icon btn-active-light p-0 w-34px h-34px border-0 me-2" onClick={this.viewInvoice(invoiceItem)}>
                                                        <span className="svg-icon svg-icon-3 me-0">
                                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                                <path fill="none" d="M0 0h24v24H0z" />
                                                                <path id="Path_5232" data-name="Path 5232" d="M21.87,11.5c-.64-1.11-4.16-6.68-10.14-6.5C6.2,5.14,3,10,2.13,11.5a1,1,0,0,0,0,1c.63,1.09,4,6.5,9.89,6.5h.25c5.53-.14,8.74-5,9.6-6.5A1,1,0,0,0,21.87,11.5ZM12.22,17c-4.31.1-7.12-3.59-8-5,1-1.61,3.61-4.9,7.61-5,4.29-.11,7.11,3.59,8,5-1.03,1.61-3.61,4.9-7.61,5Z" fill="#545454" />
                                                                <path id="Path_5233" data-name="Path 5233" d="M12,8.5A3.5,3.5,0,1,0,15.5,12,3.5,3.5,0,0,0,12,8.5Zm0,5A1.5,1.5,0,1,1,13.5,12,1.5,1.5,0,0,1,12,13.5Z" fill="#545454" />
                                                            </svg>
                                                        </span>
                                                    </button>
                                                </OverlayTrigger>
                                                {invoiceDownload === 0 ?
                                                    <OverlayTrigger placement="top" overlay={<Tooltip>Download</Tooltip>}>
                                                        <button type="button" className="btn btn-icon btn-active-light p-0 w-34px h-34px border-0" onClick={this.clickToInvoiceDownload(invoiceItem.invoiceId)}>
                                                            <span className="svg-icon svg-icon-3 me-0">
                                                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                                    <g id="Download_6506" data-name="Download 6506" transform="translate(-23412 -236)">
                                                                        <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(23412 236)" fill="none" />
                                                                        <g id="noun-download-4879420" transform="translate(23332.367 197.581)">
                                                                            <path id="Path_6615" data-name="Path 6615" d="M84.068,44.107a.609.609,0,0,1,.9-.822l2.745,3v-9.4a.609.609,0,0,1,1.217,0v9.4l2.745-3a.609.609,0,0,1,.9.822l-3.8,4.154a.608.608,0,0,1-.9,0ZM97.45,45.512a.609.609,0,0,0-.609.609v5.552h-17.8V46.121a.609.609,0,0,0-1.218,0V52.28a.608.608,0,0,0,.609.609H97.45a.608.608,0,0,0,.609-.608v-6.16a.609.609,0,0,0-.609-.609Z" transform="translate(3.814 4.128)" fill="#545454" stroke="#545454" strokeWidth="0.8" fillRule="evenodd" />
                                                                        </g>
                                                                    </g>
                                                                </svg>
                                                            </span>
                                                        </button>
                                                    </OverlayTrigger>
                                                    : invoiceDownload === 1 ?
                                                        <OverlayTrigger placement="top" overlay={<Tooltip>Downloading</Tooltip>}>
                                                            <button type="button" className="btn btn-icon btn-active-light p-0 w-34px h-34px border-0">
                                                                <div className="spinner spinner-dark spinner-sm position-relative me-2"></div>
                                                            </button>
                                                        </OverlayTrigger>
                                                        : invoiceDownload === 2 ?
                                                            <OverlayTrigger placement="top" overlay={<Tooltip>Success</Tooltip>}>
                                                                <button type="button" className="btn btn-icon btn-active-light p-0 w-34px h-34px border-0">
                                                                    <span className="svg-icon svg-icon-3 me-0">
                                                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="20" height="20">
                                                                            <path fill="none" d="M0 0h24v24H0z" />
                                                                            <path d="M10 15.172l9.192-9.193 1.415 1.414L10 18l-6.364-6.364 1.414-1.414z" fill="#6AC259" />
                                                                        </svg>
                                                                    </span>
                                                                </button>
                                                            </OverlayTrigger>
                                                            : null}
                                            </td>
                                        </tr>
                                    })
                                }
                            </tbody>
                        </Table> : <Suspense fallback={<></>}>
                            <NoInvoiceFound />
                        </Suspense>
                    }


                </div>
            </div>
            {viewInvoiceData ? <Suspense fallback={<></>}>
                <ViewInvoice viewInvoiceCallback={this.viewInvoiceCallback} modalToggle={viewInvoice} viewInvoiceData={viewInvoiceData} clickToInvoiceDownload={this.downloadInvoice} printInvoice={this.printInvoice} invoiceDownloadRequest={invoiceDownloadRequest} invoicePrintRequest={invoicePrintRequest} />
            </Suspense> : null}
        </div>
    }
}

export default Invoice;